import { useAppDispatch, useAppSelector } from 'hooks/reduxHooks';
import { FC } from 'react';
import { NotificationsType } from 'store/alert';
import styles from './Alert.module.scss';

interface SuccessAlertType {
  notifications: NotificationsType[];
}
const SuccessAlert: FC<SuccessAlertType> = ({ notifications }) => (
  <>
    {notifications.map((notification) => (
      <div className={styles.notification}>
        <span>{notification.message}</span>
      </div>
    ))}
  </>
);

const ErrorAlert: FC<SuccessAlertType> = ({ notifications }) => (
  <>
    {notifications.map((notification) => (
      <div className={styles.notification}>
        <span>{notification.message}</span>
      </div>
    ))}
  </>
);

const GlobalAlert = () => {
  const dispatch = useAppDispatch();
  const { alertIsOpen, successMessages, errorsMessages } = useAppSelector((state) => state.alert);

  if (!alertIsOpen) return null;

  return (
    <div className={styles.alertsContainer}>
      <SuccessAlert notifications={successMessages} />
      <ErrorAlert notifications={errorsMessages} />
    </div>
  );
};

export default GlobalAlert;
