/* eslint-disable no-param-reassign */
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

export interface NotificationsType {
  id: number;
  message: string;
}
export const initialState = {
  alertIsOpen: false,
  successMessages: [] as NotificationsType[],
  errorsMessages: [] as NotificationsType[],
};

const alert = createSlice({
  name: 'alert',
  initialState,
  reducers: {
    toggleAlert: (state, action: PayloadAction<boolean>) => {
      state.alertIsOpen = action.payload;
    },
    addSuccessMessage: (state, action: PayloadAction<NotificationsType>) => {
      state.successMessages.push(action.payload);
    },
    deleteSuccessMessage: (state, action: PayloadAction<NotificationsType>) => {
      state.successMessages.filter((n) => n.id !== action.payload.id);
    },
    addErrorMessage: (state, action: PayloadAction<NotificationsType>) => {
      state.errorsMessages.push(action.payload);
    },
    deleteErrorMessage: (state, action: PayloadAction<NotificationsType>) => {
      state.errorsMessages.filter((n) => n.id !== action.payload.id);
    },
  },
});

export const {
  toggleAlert,
  addErrorMessage,
  addSuccessMessage,
  deleteErrorMessage,
  deleteSuccessMessage,
} = alert.actions;
export default alert.reducer;
